import React,{ Component } from 'react';
import { connect } from 'react-redux';

import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';

import FontIcon from 'material-ui/FontIcon';
import Done from 'material-ui/svg-icons/action/done';
import Clear from 'material-ui/svg-icons/content/clear';

class NewsItem extends Component {
	constructor(props){
		super(props);

		this.state={
			text:this.props.info.text,
			link:this.props.info.link,
			time:this.props.info.time
		}
	}

	render(){
		return (
			<tr>
				<td>
					<TextField type="text"
						hintText="新闻内容"
      					floatingLabelText="请输入新闻内容"
						value={ this.state.text }
						onChange={this.changeText}
					/>
				</td>
				<td>
					<TextField type="text"
						hintText="链接地址"
      					floatingLabelText="请输入链接地址"
						value={ this.state.link }
						onChange={this.changeLink}
					/>
				</td>
				<td>
					<TextField type="text"
						hintText="时间"
      					floatingLabelText="请输入时间"
						value={ this.state.time }
						onChange={this.changeTime}
					/>
				</td>
				<td>
					<RaisedButton  primary={true} onClick={ this.addHandler } icon={ <Done/> }/>
					<RaisedButton  secondary={true} onClick={ this.deleteHandler } icon={ <Clear/> }/>
				</td>
			</tr>
		)
	}
	
	changeText=({target})=>{
		this.setState({
			text:target.value
		})
	}

	changeLink=({target})=>{
		this.setState({
			link:target.value
		})
	}

	changeTime=({target})=>{
		this.setState({
			time:target.value
		})
	}

	addHandler=()=>{
		const { text,link,time } = this.state;
		this.props.dispatch({
			type:'NEWS_UPDATE',
			payload:{
				index:this.props.index,
				info:{
					text,link,time
				}
			}
		})
	}

	deleteHandler=()=>{
		this.props.dispatch({
			type:'NEWS_DELETE',
			payload:{
				index:this.props.index
			}
		})
	}

}

export default connect((store)=>{
	return {
		news:store.news
	}
})(NewsItem)


