export function get(url,data){
	return fetch(url).then(r=>r.json())
}

export function post(url,data){
	return (
		fetch(url,{
			method:'POST',
			headers:{
				"Content-Type":"application/json",
				"Accept":"application/json"
			},
			body:JSON.stringify(data)
		}).then(r=>r.json())
	)
}


