import React,{ Component } from 'react';
import { connect } from 'react-redux';

class ModalNews extends Component {
	constructor(props){
		super(props);

		this.state={
			text:'',
			link:'',
			time:''
		}
	}

	render(){
		return(
			<div className="shadow">
				<div className="add-modal">
					<h4>添加项</h4>
					<div>
						<label>新闻内容</label>:
						<input type="text"
							className="insert"
							value={this.state.image}
							onChange={this.textHandler}
						/>
					</div>
					<div>
						<label>链接地址</label>:
						<input type="text"
							className="insert"
							value={this.state.link}
							onChange={this.linkHandler}
						/>
					</div>
					<div>
						<label>时间</label>:
						<input type="text"
							className="insert"
							value={this.state.time}
							onChange={this.timeHandler}
						/>
					</div>
					<button onClick={ this.addHandler } className="submit_button">submit</button><br/>
					<button onClick={ this.props.onClose } className="cancel_button">cancel</button>
				</div>
			</div>
		)
	}

	textHandler=({target})=>{
		this.setState({
			text:target.value
		})
	}

	linkHandler=({target})=>{
		this.setState({
			link:target.value
		})
	}

	timeHandler=({target})=>{
		this.setState({
			time:target.value
		})
	}
	
	addHandler=()=>{

		const {text,link,time} = this.state;

		if(!text || !link || !time) return;

		this.props.dispatch({
			type:'NEWS_ADD',
			payload:{
				text,link,time
			}
		})
		this.props.onClose();
	}


}
export default connect()(ModalNews)


