import React,{ Component } from 'react';
import { connect } from 'react-redux';

import SliderItem from './SliderItem';
import Modal from './Modal'

import { post } from '../../methods/Net';

import "../../styles/content.css"

import RaisedButton from 'material-ui/RaisedButton';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import TextField from 'material-ui/TextField';
import Save from 'material-ui/svg-icons/content/save';
import Add from 'material-ui/svg-icons/content/add';
import Snackbar from 'material-ui/Snackbar';

class Sliders extends Component {
	constructor(props){
		super(props);

		this.state={
			image:'',
			link:'',
			showItem:false,
			nackbarOpen:false
		};
	}
	render(){
		const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        label="Submit"
        primary={true}
        keyboardFocused={true}
        onTouchTap={this.commitHandler}
      />,
    ];
		return(
			<div>
				<table>
					<tbody>
						{this.getSliders()}
					</tbody>
				</table>
				<RaisedButton
					fullWidth={true}
					icon={<Add/>}
					onClick={this.changeShowHandler}
					className="add_button"/>
				<RaisedButton 
					primary={true}
					fullWidth={true}
					icon={<Save/>}
					className="save_button"
					onClick={ this.saveHandler }/>
				<Dialog
		          title="添加轮播图"
		          actions={actions}
		          modal={false}
		          open={this.state.showItem}
		          onRequestClose={this.handleClose}
		        >
			        <TextField 
					    hintText="填写图片地址"
					    value={ this.state.image }
					    onChange = { this.imageHandler }
					    floatingLabelText="图片地址"/>
		        	<TextField 
				       hintText="填写链接地址"
				       value={ this.state.link }
				       onChange = { this.linkHandler }
				       floatingLabelText="链接地址"/>
		        </Dialog>
		        <Snackbar
		          open={this.state.snackbarOpen}
		          message="已保存"
		          autoHideDuration={2000}
		          onRequestClose={this.handleRequestClose}
		        />
			</div>
		)
	}

	getSliders=()=>{
		return this.props.sliders.map((slider,index)=>{
			return (
				<SliderItem
					index={index}
					key={index+slider.link}
					info={slider}
				></SliderItem>
			)
		})
	}

	changeShowHandler=()=>{
		this.setState({
			showItem:true
		})
	}

	imageHandler=({target})=>{
		this.setState({
			image:target.value
		})
	}

	linkHandler=({target})=>{
		this.setState({
			link:target.value
		})
	}

	handleClose=()=>{
		this.setState({
			showItem:false
		})
	}
	
	commitHandler=()=>{

		const {image,link} = this.state;

		if(!image || !link) return;

		this.props.dispatch({
			type:'SLIDER_ADD',
			payload:{
				image,link
			}
		})

		this.setState({
			image:'',
			link:'',
			showItem:false
		})
	}

	saveHandler=()=>{
		post(`http://localhost:9999/sliders`,this.props.sliders)
		.then(r=>{
			this.setState({
				snackbarOpen:true
			})
		})
	}

	handleRequestClose = () => {
		this.setState({
			snackbarOpen:false
		})
	}
}

export default connect((store)=>{
	return{
		sliders:store.sliders
	}
})(Sliders)


