export default (state={
	sliders:[],
	news:[]
},{type,payload})=>{

	if(type==='INIT'){
		return {
			...state,
			sliders:payload.sliders,
			news:payload.news
		}
	}

	if(type==='SLIDER_UPDATE'){
		return{
			...state,
			sliders:state.sliders.map((item,index)=>{
				if(index!==payload.index) return item;
				return{
					...item,
					image:payload.info.image,
					link:payload.info.link
				}
			})
		}
	}

	if(type==="SLIDER_DELETE"){
		return {
			...state,
			sliders:state.sliders.filter((item,index)=>index!==payload.index)
		}
	}

	if(type==="NEWS_UPDATE"){
		return{
			...state,
			news:state.news.map((item,index)=>{
				if(index!==payload.index) return item;
				return {
					...item,
					text:payload.info.text,
					link:payload.info.link,
					time:payload.info.time
				}
			})
		}
	}

	if(type==="NEWS_DELETE"){
		return {
			...state,
			news:state.news.filter((item,index)=>index!==payload.index)
		}
	}

	if(type==="SLIDER_ADD"){
		return {
			...state,
			sliders:state.sliders.concat([payload])
		}
	}

	if(type==="NEWS_ADD"){
		return {
			...state,
			news:state.news.concat([payload])
		}
	}

	return {
		...state
	}
}


