import React,{ Component } from 'react';
import { connect } from 'react-redux'

import SliderItem from './NewsItem'
import ModalNews from './ModalNews'

import { post } from '../../methods/Net'

import "../../styles/content.css"

import RaisedButton from 'material-ui/RaisedButton';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import TextField from 'material-ui/TextField';
import Save from 'material-ui/svg-icons/content/save';
import Add from 'material-ui/svg-icons/content/add';
import Snackbar from 'material-ui/Snackbar';

class News extends Component {
	constructor(props){
		super(props);

		this.state={
			showItem:false
		}
	}
	render(){
		return(
			<div>
				<table>
					<tbody>
						{this.getSliders()}
					</tbody>
				</table>
				<RaisedButton 
					fullWidth={true}
					icon={<Add/>}
					onClick={this.changeShowHandler}
					className="add_button"
				/><br/>
				{this.state.showItem?
				<ModalNews onClose={()=>{this.setState({showItem:false})}}/>:
				null
				}
				<RaisedButton
					fullWidth={true}
					icon={<Save/>}
					primary={true}
					className="save_button"
					onClick={ this.saveNewsHandler }
				/>
			</div>
		)
	}

	getSliders=()=>{
		return this.props.news.map((item,index)=>{
			return (
				<SliderItem
					index={index}
					key={index+item.link}
					info={item}
				></SliderItem>
			)
		})
	}

	changeShowHandler=()=>{
		this.setState({
			showItem:true
		})
	}

	saveNewsHandler=()=>{
		post(`http://localhost:9999/news`,this.props.news)
		.then(r=>{
			alert('Success')
		})
	}
}

export default connect((store)=>{
	return{
		news:store.news
	}
})(News)


