import React,{ Component } from 'react';
import { connect } from 'react-redux';

import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';

class Modal extends Component {
	constructor(props){
		super(props);

		this.state={
			image:'',
			link:'',
			showItem:false
		}
	}

	render(){
		const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        label="Submit"
        primary={true}
        keyboardFocused={true}
        onTouchTap={this.handleClose}
      />,
    ];
		return(
			<div>
				
				<Dialog
		          title="Dialog With Actions"
		          actions={actions}
		          modal={false}
		          open={this.state.showItem}
		          onRequestClose={this.handleClose}
		        >
			        <TextField 
					    hintText="填写图片地址"
					    value={ this.state.image }
					    onChange = { this.imageHandler }
					    floatingLabelText="图片地址"/>
		        	<TextField 
				       hintText="填写链接地址"
				       value={ this.state.link }
				       onChange = { this.linkHandler }
				       floatingLabelText="链接地址"/>
		        </Dialog>
			</div>
		)
	}

	imageHandler=({target})=>{
		this.setState({
			image:target.value
		})
	}

	linkHandler=({target})=>{
		this.setState({
			link:target.value
		})
	}

	handleClose=()=>{
		this.setState({
			showItem:false
		})
	}
	
	addHandler=()=>{

		const {image,link} = this.state;

		if(!image || !link) return;

		this.props.dispatch({
			type:'SLIDER_ADD',
			payload:{
				image,link
			}
		})
		this.props.onClose();
	}


}
// export default connect()(Modal)


