import React,{ Component } from 'react';
import { connect } from 'react-redux';

import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';

import FontIcon from 'material-ui/FontIcon';
import Done from 'material-ui/svg-icons/action/done';
import Clear from 'material-ui/svg-icons/content/clear';

class SliderItem extends Component {
	constructor(props){
		super(props);

		this.state={
			image:this.props.info.image,
			link:this.props.info.link
		}
	}

	render(){
		return (
			<tr>
				<td>
					<TextField type="text"
						hintText="图片地址"
      					floatingLabelText="请输入图片地址"
						value={ this.state.image }
						onChange={this.changeImage}
					/>
				</td>
				<td>
					<TextField type="text"
						hintText="链接地址"
      					floatingLabelText="请输入链接地址"
						value={ this.state.link }
						onChange={this.changeLink}
					/>
				</td>
				<td>
					<RaisedButton  primary={true} onClick={ this.addHandler } icon={<Done />}/>
					<RaisedButton  secondary={true} onClick={ this.deleteHandler } icon={<Clear />}/>
				</td>
			</tr>
		)
	}
	
	changeImage=({target})=>{
		this.setState({
			image:target.value
		})
	}

	changeLink=({target})=>{
		this.setState({
			link:target.value
		})
	}

	addHandler=()=>{
		const { image,link } = this.state;
		this.props.dispatch({
			type:'SLIDER_UPDATE',
			payload:{
				index:this.props.index,
				info:{
					image,link
				}
			}
		})
	}

	deleteHandler=()=>{
		this.props.dispatch({
			type:'SLIDER_DELETE',
			payload:{
				index:this.props.index,
			}
		})
	}

}

export default connect((store)=>{
	return {
		sliders:store.sliders
	}
})(SliderItem)


